import React from 'react'
import { Link } from 'react-router-dom'
import { logDOM } from "@testing-library/react";
import { t } from 'i18next';
const Hosting = () => {
    return (
        <>
            <div class="main-content" id="miniaresult">
                <div class="page-content">
                    <div class="container-fluid">

                        <div class="row pt-5">
                            <div class="col-12">
                                <div class="page-title-box d-sm-flex align-items-center justify-content-between">
                                    <h4 class="mb-sm-0 font-size-18">{t('server')}</h4>


                                </div>
                            </div>
                        </div>

                        <div class="row">

                            <div class="col-md-12 col-12 col-xl-3">

                                <div class="card">
                                    <img class="card-img-top img-fluid" src="hosting_files/info.jpg" alt="Germany" />
                                    <div class="card-body">
                                        <h4 class="card-title">Germany</h4>
                                        <p class="card-text">{t('text')}</p>
                                        <Link to="/tariflar" class="card-link btn btn-primary waves-effect waves-light">{t('tarif')}</Link>
                                        <a href="javascript: void(0);" class="card-link">{t('malumot')}</a>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>

                <footer class="footer">
                    <div class="container-fluid">
                        <div class="row">
                            <div class="col-sm-6">{t('huquqlar')}<span class="text-muted mb-3 lh-1 d-block text-truncate">{t('raqami')}</span>
                            </div>
                            <div class="col-sm-6">
                                <div class="text-sm-end d-none d-sm-block">
                                    <a href="https://sysdc.uz/gorders/hosting/servers/#!" class="text-muted">{t('haqida')}</a> | <a href="https://sysdc.uz/gorders/hosting/servers/#!" class="text-muted">{t('aloqa')}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>

            </div>
        </>
    )
}

export default Hosting