import { logDOM } from "@testing-library/react";
import React from "react";
import App from "../App";
import { Icon } from '@iconify/react';
import { useTranslation } from 'react-i18next';
import { useEffect } from 'react';
import i18next from 'i18next';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { FontawesomeObject } from "@fortawesome/fontawesome-svg-core";
import LanguageDetector from 'i18next-browser-languagedetector';
import HttpApi from 'i18next-http-backend';
import "/node_modules/flag-icons/css/flag-icons.min.css";
import cookie from 'js-cookie'
import { Link } from 'react-router-dom';
const language = [
  {
    code: 'ru',
    name: 'Russian',
    country_code: 'ru'
  },
  {
    code: 'en',
    name: 'English',
    country_code: 'gb'
  },
  {
    code: 'uz',
    name: 'Uzbek',
    country_code: 'uz'
  }
]
const GlobeIcon = ({ width = 24, height = 24 }) => (
  <svg xmlns="http://www.w3.org/2000/svg" width={width} height={height} fill="currentColor" class="bi bi-globe" viewBox="0 0 16 16">
    <path d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm7.5-6.923c-.67.204-1.335.82-1.887 1.855A7.97 7.97 0 0 0 5.145 4H7.5V1.077zM4.09 4a9.267 9.267 0 0 1 .64-1.539 6.7 6.7 0 0 1 .597-.933A7.025 7.025 0 0 0 2.255 4H4.09zm-.582 3.5c.03-.877.138-1.718.312-2.5H1.674a6.958 6.958 0 0 0-.656 2.5h2.49zM4.847 5a12.5 12.5 0 0 0-.338 2.5H7.5V5H4.847zM8.5 5v2.5h2.99a12.495 12.495 0 0 0-.337-2.5H8.5zM4.51 8.5a12.5 12.5 0 0 0 .337 2.5H7.5V8.5H4.51zm3.99 0V11h2.653c.187-.765.306-1.608.338-2.5H8.5zM5.145 12c.138.386.295.744.468 1.068.552 1.035 1.218 1.65 1.887 1.855V12H5.145zm.182 2.472a6.696 6.696 0 0 1-.597-.933A9.268 9.268 0 0 1 4.09 12H2.255a7.024 7.024 0 0 0 3.072 2.472zM3.82 11a13.652 13.652 0 0 1-.312-2.5h-2.49c.062.89.291 1.733.656 2.5H3.82zm6.853 3.472A7.024 7.024 0 0 0 13.745 12H11.91a9.27 9.27 0 0 1-.64 1.539 6.688 6.688 0 0 1-.597.933zM8.5 12v2.923c.67-.204 1.335-.82 1.887-1.855.173-.324.33-.682.468-1.068H8.5zm3.68-1h2.146c.365-.767.594-1.61.656-2.5h-2.49a13.65 13.65 0 0 1-.312 2.5zm2.802-3.5a6.959 6.959 0 0 0-.656-2.5H12.18c.174.782.282 1.623.312 2.5h2.49zM11.27 2.461c.247.464.462.98.64 1.539h1.835a7.024 7.024 0 0 0-3.072-2.472c.218.284.418.598.597.933zM10.855 4a7.966 7.966 0 0 0-.468-1.068C9.835 1.897 9.17 1.282 8.5 1.077V4h2.355z" />
  </svg>
)
const Navbar = () => {
  const currenLanguageCode = cookie.get('i18next') || 'en'
  const currentlanguage = language.find(l => l.code === currenLanguageCode)
  const { t } = useTranslation()
  useEffect(() => {
    document.body.dir = currentlanguage.dir || 'ltr'
    document.title = t('app_title')
  }, [currentlanguage, t])
  return (
    <div >
      {/* <ul className="d-flex justify-content-around" type="none">
                <li className="me-2"><Link to="/">Home</Link></li>
                <li className="me-2"><Link to="/about">About</Link></li>
                <li className="me-2"><Link to="/contact">Contact</Link></li>
                <li className="me-2"><Link to="/news">News</Link></li>

  </ul> */}
      <div id="layout-wrapper">

        <header id="page-topbar">
          <div class="navbar-header md-ps-5 bg-white md-me-5 md-pe-5">
            <div class="d-flex md-ps-5 md-pe-5">

              <div class="navbar-brand-box bg-white">
                <a href="/" class="logo logo-dark">
                  <span class="logo-sm">
                    <img src="//sysdc.uz/assets/images/logo_icon.png" alt="" height="35" />
                  </span>
                  <span class="logo-lg">
                    <img src="//sysdc.uz/assets/images/logo_icon.png" alt="" height="35" /> <span class="logo-txt">SYSDC.UZ</span>
                  </span>
                </a>

                <a href="/" class="logo logo-light">
                  <span class="logo-sm">
                    <img src="//sysdc.uz/assets/images/logo_icon_light.png" alt="" height="40" />
                  </span>
                  <span class="logo-lg">
                    <img src="//sysdc.uz/assets/images/logo_icon_light.png" alt="" height="40" /> <span class="logo-txt">SYSDC.UZ</span>
                  </span>
                </a>
              </div>





            </div>
            <div class="d-flex pe-5">

              <div class="dropdown d-none d-sm-inline-block">
                <button type="button" class="btn header-item btn-link" data-bs-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false">
                  <GlobeIcon />
                </button>
                <div class="dropdown-menu dropdown-menu-end">

                  
                  {language.map(({ code, name, country_code }) => (
                    <li key={country_code}>
                      <button className="dropdown-item"
                        onClick={() => i18next.changeLanguage(code)}
                        disabled={code === currenLanguageCode}
                      >
                        <span className={`fi fi-${country_code}`} style={{ opacity: code === currentlanguage ? .5 : 1 }}></span>&nbsp;
                        {name}
                      </button>
                    </li>
                  ))}

                </div>
              </div>



              <div class="dropdown d-none d-sm-inline-block">
                <button type="button" class="btn header-item" id="mode-setting-btn">
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-moon icon-lg layout-mode-dark"><path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path></svg>
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-sun icon-lg layout-mode-light"><circle cx="12" cy="12" r="5"></circle><line x1="12" y1="1" x2="12" y2="3"></line><line x1="12" y1="21" x2="12" y2="23"></line><line x1="4.22" y1="4.22" x2="5.64" y2="5.64"></line><line x1="18.36" y1="18.36" x2="19.78" y2="19.78"></line><line x1="1" y1="12" x2="3" y2="12"></line><line x1="21" y1="12" x2="23" y2="12"></line><line x1="4.22" y1="19.78" x2="5.64" y2="18.36"></line><line x1="18.36" y1="5.64" x2="19.78" y2="4.22"></line></svg>
                </button>
              </div>

              <div class="dropdown d-inline-block d-sm-inline-block ms-2">
                <button type="button" class="btn header-item" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  <i class="far fa-user fa-lg"></i>
                </button>
                <div class="dropdown-menu dropdown-menu-lg dropdown-menu-end" >
                  <div class="p-2">
                    <div class="row g-0">

                      <div class="col">
                        <Link class="dropdown-icon-item" to="/signin">
                          <i class="fas fa-user fa-lg"></i>
                          <span>{t('log_in')}</span>
                        </Link>
                      </div>
                      <div class="col">
                        <Link class="dropdown-icon-item" to="/signup">
                          <i class="fas fa-user-plus fa-lg"></i>
                          <span>{t('sign_up')}</span>
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>







            </div>

          </div>
        </header>
        <div class="topnav active md-ms-3 md-ps-5">
          <div class="container-fluid active ps-5">
            <nav class="navbar navbar-light navbar-expand-lg topnav-menu active">

              <div class="collapse navbar-collapse active" id="topnav-menu-content">
                <ul class="navbar-nav active gap-lg-5">

                  <li class="nav-item dropdown active">
                    <Link class="nav-link dropdown-toggle arrow-none active" to="/" id="topnav-dashboard" role="button">
                    <i class="fa-solid fa-house"></i> <span data-key="t-dashboards">{t('app_title')}</span>
                    </Link>
                  </li>

                  <li class="nav-item dropdown">
                    <Link class="nav-link dropdown-toggle arrow-none" target="_self" to="/domen" role="button">
                    <i class="fa-solid fa-globe"></i> <span data-key="t-horizontal" >{t('domain')}</span>
                    </Link>
                  </li>
                  <li class="nav-item dropdown">
                    <Link class="nav-link dropdown-toggle arrow-none" target="_self" to="/sms" role="button">
                    <i class="fa-regular fa-message"></i> <span data-key="t-horizontal">{t('sms')}</span>
                    </Link>
                  </li>

                  <li class="nav-item dropdown">
                    <Link class="nav-link dropdown-toggle arrow-none" to="/" id="topnav-pages" role="button">
                    <i class="fa-solid fa-sitemap"></i> <span data-key="t-apps">{t('hostinglar')}
                      </span>
                    </Link>
                    <div class="dropdown-menu" aria-labelledby="topnav-pages">

                      <Link to="/hosting" class="dropdown-item" data-key="t-calendar">{t('hosting')}</Link>
                      <Link to="/reselling" class="dropdown-item" data-key="t-chat">{t('reselling')}</Link>





                      <Link to="/vds" class="dropdown-item" data-key="t-chat">{('vds')}</Link>
                      <Link to="/dedicated" class="dropdown-item" data-key="t-chat">{t('dedicated')}</Link></div>
                  </li>



                </ul>
              </div>
            </nav>
          </div>
        </div>
        <div class="main-content" id="miniaresult">
          
          <div class="float-button-panel pulse">
            <div class="float-button-circle float-telegram shadow-sm">
              <a href="//t.me/user0601" target="_blank" class="float-button-link">
                <i class="fab fa-telegram float-button-icon"></i>
                <span class="d-none d-xl-inline float-button-text">Telegram Оператор</span>
              </a>
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}
export default Navbar;