import React from 'react'
import cookie from 'js-cookie'
import { useTranslation } from 'react-i18next';
import { useEffect } from 'react';
const language = [
    {
      code: 'ru',
      name: 'Russian',
      country_code: 'ru'
    },
    {
      code: 'en',
      name: 'English',
      country_code: 'gb'
    },
    {
      code: 'uz',
      name: 'Uzbek',
      country_code: 'uz'
    }
  ]
const Dedicated = () => {
    const currenLanguageCode = cookie.get('i18next') || 'en'
    const currentlanguage = language.find(l => l.code === currenLanguageCode)
    const { t } = useTranslation()
    useEffect(() => {
        document.body.dir = currentlanguage.dir || 'ltr'
        document.title = t('app_title')
    }, [currentlanguage, t])
  return (
    <div>
    <div class="pt-5">
        <div class="page-content">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-12">
                        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
                            <h4 class="mb-sm-0 font-size-18 mt-3">{t('server2')}- Germany</h4>

                            <div class="page-title-right">
                                <ul class="nav justify-content-end nav-tabs-custom rounded card-header-tabs" id="pills-tab" role="tablist">
                                    <li class="nav-item">
                                        <a class="nav-link px-3 rounded dayly" id="dayly" data-bs-toggle="pill" href="https://sysdc.uz/gorders/hosting/rates/1#day" role="tab" aria-controls="day" aria-selected="false">{t('Kunlik')}</a>
                                    </li>
                                    <li class="nav-item">
                                        <a class="nav-link px-3 rounded monthly" id="monthly" data-bs-toggle="pill" href="https://sysdc.uz/gorders/hosting/rates/1#month" role="tab" aria-controls="month" aria-selected="false">{t('Oylik')}</a>
                                    </li>
                                    <li class="nav-item">
                                        <a class="nav-link px-3 rounded yearly active" id="yearly" data-bs-toggle="pill" href="https://sysdc.uz/gorders/hosting/rates/1#year" role="tab" aria-controls="year" aria-selected="true">{t('Yillik')}</a>
                                    </li>
                                </ul>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <footer class="footer">
                    <div class="container-fluid">
                        <div class="row">
                            <div class="col-sm-6">{t('huquqlar')}<span class="text-muted mb-3 lh-1 d-block text-truncate">{t('raqami')}</span>
                            </div>
                            <div class="col-sm-6">
                                <div class="text-sm-end d-none d-sm-block">
                                    <a href="https://sysdc.uz/gorders/hosting/servers/#!" class="text-muted">{t('haqida')}</a> | <a href="https://sysdc.uz/gorders/hosting/servers/#!" class="text-muted">{t('aloqa')}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>
        </div>
    </div>
</div>
  )
}

export default Dedicated