import React from 'react'
import { useTranslation } from 'react-i18next';
import cookie from 'js-cookie'
import { useEffect } from 'react'
import { Link } from 'react-router-dom';
const language = [
    {
      code: 'ru',
      name: 'Russian',
      country_code: 'ru'
    },
    {
      code: 'en',
      name: 'English',
      country_code: 'gb'
    },
    {
      code: 'uz',
      name: 'Uzbek',
      country_code: 'uz'
    }
  ]
  const GlobeIcon = ({ width = 24, height = 24 }) => (
    <svg xmlns="http://www.w3.org/2000/svg" width={width} height={height} fill="currentColor" class="bi bi-globe" viewBox="0 0 16 16">
      <path d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm7.5-6.923c-.67.204-1.335.82-1.887 1.855A7.97 7.97 0 0 0 5.145 4H7.5V1.077zM4.09 4a9.267 9.267 0 0 1 .64-1.539 6.7 6.7 0 0 1 .597-.933A7.025 7.025 0 0 0 2.255 4H4.09zm-.582 3.5c.03-.877.138-1.718.312-2.5H1.674a6.958 6.958 0 0 0-.656 2.5h2.49zM4.847 5a12.5 12.5 0 0 0-.338 2.5H7.5V5H4.847zM8.5 5v2.5h2.99a12.495 12.495 0 0 0-.337-2.5H8.5zM4.51 8.5a12.5 12.5 0 0 0 .337 2.5H7.5V8.5H4.51zm3.99 0V11h2.653c.187-.765.306-1.608.338-2.5H8.5zM5.145 12c.138.386.295.744.468 1.068.552 1.035 1.218 1.65 1.887 1.855V12H5.145zm.182 2.472a6.696 6.696 0 0 1-.597-.933A9.268 9.268 0 0 1 4.09 12H2.255a7.024 7.024 0 0 0 3.072 2.472zM3.82 11a13.652 13.652 0 0 1-.312-2.5h-2.49c.062.89.291 1.733.656 2.5H3.82zm6.853 3.472A7.024 7.024 0 0 0 13.745 12H11.91a9.27 9.27 0 0 1-.64 1.539 6.688 6.688 0 0 1-.597.933zM8.5 12v2.923c.67-.204 1.335-.82 1.887-1.855.173-.324.33-.682.468-1.068H8.5zm3.68-1h2.146c.365-.767.594-1.61.656-2.5h-2.49a13.65 13.65 0 0 1-.312 2.5zm2.802-3.5a6.959 6.959 0 0 0-.656-2.5H12.18c.174.782.282 1.623.312 2.5h2.49zM11.27 2.461c.247.464.462.98.64 1.539h1.835a7.024 7.024 0 0 0-3.072-2.472c.218.284.418.598.597.933zM10.855 4a7.966 7.966 0 0 0-.468-1.068C9.835 1.897 9.17 1.282 8.5 1.077V4h2.355z" />
    </svg>
  )
const SignUp = () => {
  const currenLanguageCode = cookie.get('i18next') || 'en'
    const currentlanguage = language.find(l => l.code === currenLanguageCode)
    const { t } = useTranslation()
    useEffect(() => {
      document.body.dir = currentlanguage.dir || 'ltr'
      document.title = t('app_title')
    }, [currentlanguage, t])
  return (
    <>
      <div class="auth-page pt-5">
        <div class="container-fluid p-0">
          <div class="row g-0">
            <div class="col-xxl-3 col-lg-4 col-md-5">
              <div class="auth-full-page-content d-flex p-sm-5 p-4">
                <div class="w-100">
                  <div class="d-flex flex-column h-100">
                    <div class="mb-4 mb-md-5 text-center">
                      <a href="https://sysdc.uz/" class="d-block auth-logo">
                        <img src="Roʻyxat_files/logo_icon.png" alt="" height="35" /> <span class="logo-txt">SYSDC</span>
                      </a>
                    </div>
                    <div class="auth-content my-auto">
                      <div class="text-center">
                        <h5 class="mb-0">{t('signup')}</h5>
                        <p class="text-muted mt-2">{t('xizmat')}</p>
                      </div>
                      <form class="needs-validation mt-4 pt-2" novalidate="" action="https://sysdc.uz/user/signup?" method="POST">
                        <div class="mb-3">
                          <label for="username" class="form-label">{t('login')}</label>
                          <input type="text" class="form-control" id="username" name="username" placeholder="" required="" />
                          <div class="invalid-feedback">
                            Iltimos, longiningizni kiriting                                                </div>
                        </div>

                        <div class="mb-3">
                          <label for="phone" class="form-label">{t('phone')}</label>
                          <input type="number" class="form-control" id="phone" name="userphone" placeholder={t('raqam')} required="" />
                          <div class="invalid-feedback">
                            Iltimos, telefon raqamni kiriting                                                </div>
                        </div>

                        <div class="mb-3">
                          <label for="useremail" class="form-label">{t('email')}</label>
                          <input type="email" class="form-control" id="useremail" name="useremail" placeholder={t('pochta')} required="" />
                          <div class="invalid-feedback">
                            Iltimos, elektron pochtani kiriting                                                </div>
                        </div>


                        <div class="mb-3">
                          <label for="userpassword" class="form-label">{t('parol')}</label>
                          <input type="password" class="form-control" id="userpassword" name="userpassword" placeholder={t('kiriting')} required="" />
                          <div class="invalid-feedback">
                            Iltimos, parolni kiriting                                                </div>
                        </div>
                        <input type="hidden" class="form-control" id="userlanguage" name="userlanguage" value="0" />
                        <input type="hidden" class="form-control" id="usercountry" name="usercountry" value="0" />
                        <div class="mb-4">
                          <p class="mb-0"><a href='/'>{t('foydalanish')}</a></p>
                        </div>
                        <div class="mb-3">
                          <button class="btn btn-primary w-100 waves-effect waves-light" name="send" value="true" type="submit">{t('signup')}</button>
                        </div>
                      </form>

                      <div class="mt-5 text-center">
                        <p class="text-muted mb-0">{t('akkount')} <Link to="/signin" class="text-primary fw-semibold"> {t('kirish')} </Link> </p>
                      </div>
                    </div>
                    <div class="mt-4 mt-md-5 text-center">
                      {t('huquqlar')}<span class="text-muted lh-1 d-block text-truncate">{t('raqami')}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xxl-9 col-lg-8 col-md-7">
              <div class="auth-bg pt-md-5 p-4 d-flex">
                <div class="bg-overlay bg-primary"></div>
                <ul class="bg-bubbles">
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
                <div class="row justify-content-center align-items-center">
                  <div class="col-xl-7">
                    <div class="p-0 p-sm-4 px-xl-0">
                      <div id="reviewcarouselIndicators" class="carousel slide" data-bs-ride="carousel">
                        <div class="carousel-indicators carousel-indicators-rounded justify-content-start ms-0 mb-0">
                          <button type="button" data-bs-target="#reviewcarouselIndicators" data-bs-slide-to="0" class="" aria-label="Slide 1"></button>
                          <button type="button" data-bs-target="#reviewcarouselIndicators" data-bs-slide-to="1" aria-label="Slide 2" class=""></button>
                          <button type="button" data-bs-target="#reviewcarouselIndicators" data-bs-slide-to="2" aria-label="Slide 3" class="active" aria-current="true"></button>
                        </div>
                        <div class="carousel-inner">
                          <div class="carousel-item">
                            <div class="testi-contain text-white">
                              <i class="bx bxs-quote-alt-left text-success display-6"></i>

                              <h4 class="mt-4 fw-medium lh-base text-white">“Maqsadga erishib oladigan narsangizdan maqsadga erishib yetishadigan darajangiz muhimroqdir.”</h4>
                              <div class="mt-4 pt-3 pb-5">
                                <div class="d-flex align-items-start">
                                  <div class="flex-shrink-0">
                                    <img src="Roʻyxat_files/HenryDavidThoreau.jpg" class="avatar-md img-fluid rounded-circle" alt="..." />
                                  </div>
                                  <div class="flex-grow-1 ms-3 mb-4">
                                    <h5 class="font-size-18 text-white">Henry David Thoreau
                                    </h5>
                                    <p class="mb-0 text-white-50">Faylasuf olim</p>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>


                          <div class="carousel-item">
                            <div class="testi-contain text-white">
                              <i class="bx bxs-quote-alt-left text-success display-6"></i>

                              <h4 class="mt-4 fw-medium lh-base text-white">“Men ishlayapman dema. Nima ish qilganingni korsat!”
                              </h4>
                              <div class="mt-4 pt-3 pb-5">
                                <div class="d-flex align-items-start">
                                  <div class="flex-shrink-0">
                                    <img src="Roʻyxat_files/TomasRobertDyuar.jpg" class="avatar-md img-fluid rounded-circle" alt="..." />
                                  </div>
                                  <div class="flex-grow-1 ms-3 mb-4">
                                    <h5 class="font-size-18 text-white">Tomas Robert Dyuar
                                    </h5>
                                    <p class="mb-0 text-white-50">Tadbirkor</p>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>

                          <div class="carousel-item active">
                            <div class="testi-contain text-white">
                              <i class="bx bxs-quote-alt-left text-success display-6"></i>

                              <h4 class="mt-4 fw-medium lh-base text-white">“Oʻqish — bu vaqt sarflash emas. Oʻqish — harakat qilish.”</h4>
                              <div class="mt-4 pt-3 pb-5">
                                <div class="d-flex align-items-start">
                                  <img src="Roʻyxat_files/StivJobs.jpg" class="avatar-md img-fluid rounded-circle" alt="..." />
                                  <div class="flex-1 ms-3 mb-4">
                                    <h5 class="font-size-18 text-white">Stiv jobs</h5>
                                    <p class="mb-0 text-white-50">Olim va tadbirkor
                                    </p>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
   
  )
}

export default SignUp